.text {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  color: rgb(70, 10, 139);
  font-weight: 700;
}
.btn {
  width: 200px;
  padding: 5px;
  background: rgb(73, 9, 100);
  color: white;
  border: transparent;
  border-radius: 4px;
  cursor: pointer;
  &:hover {
    color: rgb(58, 25, 58);
    background: rgb(124, 83, 141);
  }
}
.input {
  margin-bottom: 10px;
  border-radius: 4px;
  border: transparent;
  padding: 5px;
  width: 290px;
}
